export function isNode(obj) {
    return !!obj && !!obj.ownerDocument;
}
export function replaceContent(content, node) {
    insertBefore(node, content[0]);
    removeNodes(content);
    return node;
}
export function removeNodes(nodes) {
    const range = document.createRange();
    range.setStartBefore(nodes[0]);
    range.setEndAfter(nodes[nodes.length - 1]);
    range.deleteContents();
}
export function insertBefore(node, before) {
    before.parentNode.insertBefore(node, before);
}
export function moveTemplate(template, node) {
    let currentNode = node;
    template.content.forEach(node => {
        currentNode.parentNode.insertBefore(node, currentNode.nextSibling);
        currentNode = node;
    });
}
export function getNodeIndex(node) {
    let index = 0;
    while ((node = node.previousSibling))
        index++;
    return index;
}
export function getNodePath(node) {
    const path = [];
    while (node.parentNode) {
        path.unshift(getNodeIndex(node));
        node = node.parentNode;
    }
    return path;
}
export function getNodeByPath(node, path) {
    for (let i = 0, l = path.length; i < l; i++)
        node = node.childNodes[path[i]];
    return node;
}
